.open {
  transform: translateX(-220px);
  transition-duration: 0.5s;
  position: relative;
  z-index: 100;
}
@media only screen and (min-width: 992px) {
  .open {
    display: none;
  }
}
.overlay {
  position: fixed;
  width: 220px;
  background: #f4f6f6;
  height: 100vh;
  top: 66px;
  right: -220px;
  overflow-y: scroll;
  transition: all .3s linear;
}
@media only screen and (min-width: 992px) {
  .overlay {
    position: inherit;
    width: inherit;
    height: inherit;
    background: inherit;
    overflow-y: inherit;
  }
}
.overlay::-webkit-scrollbar-track {
  border-radius: 10px;
  background-color: transparent;
}
.overlay::-webkit-scrollbar {
  width: 6px;
  background-color: transparent;
}
.overlay::-webkit-scrollbar-thumb {
  border-radius: 10px;
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  background-color: #11569d;
  background-color: #f0d91a;
}
.burger {
  position: relative;
  width: 50px;
  height: 50px;
  transition: all 0.6s ease-in-out;
  z-index: 1000;
  background: transparent;
  border: none;
  outline: none;
  display: block;
}
.burger:hover {
  cursor: pointer;
}
@media only screen and (min-width: 992px) {
  .burger {
    display: none;
  }
}
.burger i {
  display: block;
  width: 40px;
  height: 2px;
  background: #f0d91a;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  transition: all 0.2s ease-in-out;
}
.burger i:before,
.burger i:after {
  content: '';
  position: absolute;
  height: 2px;
  background: #f0d91a;
  width: 40px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  transition: all 0.2s ease-in-out;
}
.burger i:before {
  margin-top: -12px;
}
.burger i:after {
  margin-top: 12px;
}
.burger:hover i:before {
  margin-top: -14px;
}
.burger:hover i:after {
  margin-top: 14px;
}
.burger.active i {
  background: transparent;
}
.burger.active i:before,
.burger.active i:after {
  left: -5px;
  width: 50px;
}
.burger.active i:before {
  transform: rotate(45deg);
  top: 12px;
}
.burger.active i:after {
  transform: rotate(-45deg);
  top: -12px;
}
.burger.active:hover i:before,
.burger.active:hover i:after {
  width: 60px;
  margin: 0;
  left: -10px;
  top: 0;
}
/* END RESPONSIVE MENU TO 992px*/
